body {
  background: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px; }

.fico {
  display: inline-block;
  font-size: 3rem; }
  .fico::before {
    display: block; }

.header {
  width: 100%;
  height: 10vh;
  border: 4px solid #000000;
  background: #fbfab5;
  position: fixed;
  top: 0;
  z-index: 100; }

.footer {
  width: 100%;
  height: 10vh;
  border: 2px solid #000000;
  background: #fbfab5;
  position: fixed;
  bottom: 0; }

#nav_menu-toggle {
  display: block;
  position: fixed;
  margin-top: 2vh;
  left: 20px;
  z-index: 1;
  user-select: none; }
  #nav_menu-toggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -8px;
    left: -7px;
    cursor: pointer;
    opacity: 0;
    z-index: 2; }
  #nav_menu-toggle span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #000000;
    border-radius: 3px;
    z-index: 1; }
  #nav_menu-toggle input:checked ~ .menu {
    transform: scale(1, 1); }

#nav_menu-toggle:hover {
  background: #cccccc; }

.menu {
  position: fixed;
  width: 300px;
  height: 80vh;
  top: 10vh;
  padding: 50px 20px;
  left: 0;
  background: #ededed;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }
  @media (max-width: 640px) {
    .menu {
      width: 100%;
      height: auto;
      padding: 10px 10px;
      transform: translate(0, -100%);
      display: flex;
      flex-direction: column;
      align-items: center; } }
  .menu .nav_item {
    border: 2px solid #000000;
    width: 250px;
    height: 50px;
    margin-bottom: 20px; }

.article {
  margin-left: 70px;
  font-size: 20px;
  letter-spacing: 2px;
  word-spacing: 7px; }
  @media (max-width: 640px) {
    .article {
      margin-left: 0;
      margin-top: 50px; } }

.main {
  padding-top: 10vh;
  padding-bottom: 10vh; }
